import React from "react"
import md5 from "md5"
import ipfsClient from 'ipfs-http-client'
import styled from "styled-components"
import BigNumber from "bignumber.js"
import { getBalanceNumber } from "utils/formatBalance"
import { Text, Button, Heading, Flex, Box, BaseLayout, LinkExternal, Link, useModal } from "leek-uikit"
import { useBillboardCollectionContract } from "hooks/useContract"
import truncateWalletAddress from "utils/truncateWalletAddress"
import { useWallet } from '@binance-chain/bsc-use-wallet'
import UnlockButton from "components/UnlockButton"
import { getChainExplorerUrl } from "utils/chainExplorer"
import ConfirmationPendingContent from "../Modal/ConfirmationPendeingContent"
import BillboardBidModal from "../Modal/BillboardBidModal"
import { GET_BILLBOARD_HASH } from '../store/reducer';
import { store } from "../store/store"

const chainId = process.env.REACT_APP_CHAIN_ID
const ipfs = ipfsClient({ host: 'ipfs.infura.io', port: 5001, protocol: 'https' })

export interface Trait {
    attributes: Attribute[];
    image: string;
    description: string;
    name: string;
    id?: string;
}

interface Attribute {
    trait_type: string;
    value: string;
}

const BillboardLayout = styled(BaseLayout)`
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 32px;
  margin: 0 auto;
  padding:30px;
  align-items: center;

  ${({ theme }) => theme.mediaQueries.sm} {
    grid-template-columns: 0.8fr 1fr;
  }
`

const BillboardDetails = (props) => {
    const { info, baseInfo, setShowForm, mintPrice } = props;
    const { id, ipfsHash, desc, bidLevel, owner, twitter, city } = info;
    const basePrice = baseInfo && baseInfo.basePrice
    const formatedBasePrice = getBalanceNumber(new BigNumber(basePrice));
    const { account } = useWallet()
    const isQualified = account === owner
    const billboardCollectionContract = useBillboardCollectionContract()

    const bid = () => {
        setShowForm(true)
    }

    const [onPresentTraitUploadingModal] = useModal(<ConfirmationPendingContent title="Traits Uploading to IPFS" onDismiss={() => { return null }} />)
    const [onPresentBillboardBidModal] = useModal(<BillboardBidModal onDismiss={() => { return null }} />)
    const [onPresentConfirmationModal] = useModal(<ConfirmationPendingContent title="Waiting for Confirmation" onDismiss={() => { return null }} />)

    const generateNFT = async () => {
        onPresentTraitUploadingModal()
        const string = ipfsHash + city + owner + bidLevel
        const uniqueIdentifier = md5(string).slice(0, 7)

        const trait: Trait = {
            attributes: [
                {
                    trait_type: "Level",
                    value: bidLevel
                },
                {
                    trait_type: "City",
                    value: city
                },
                {
                    trait_type: "Purchased Price",
                    value: `${formatedBasePrice * bidLevel / 2} LEEK`
                }
            ],
            image: `https://ipfs.infura.io/ipfs/${ipfsHash}`,
            description: desc,
            name: `WorldMapBillBoards NFT #${uniqueIdentifier}`
        }

        try {
            const response = await ipfs.add(Buffer.from(JSON.stringify(trait)));

            if (response) {
                const { hash } = response[0];
                const tokenURI = `https://ipfs.infura.io/ipfs/${hash}`
                onPresentConfirmationModal()
                const result = await billboardCollectionContract.methods.mint(id, tokenURI).send({ from: account, value: mintPrice })
                if (result) {
                    const action = {
                        type: GET_BILLBOARD_HASH,
                        billboardHash: result.transactionHash,
                    }
                    store.dispatch(action)
                    onPresentBillboardBidModal()
                }
            }
        } catch (err) {
            console.error("Unable to mint collections", err)
        }
    }

    return (
        <BillboardLayout>
            <img src={`https://ipfs.infura.io/ipfs/${ipfsHash}`} alt="city" width={300} />
            <Box>
                <div style={{ marginBottom: "10px", maxWidth: "300px" }}>
                    <Heading size="lg" color="secondary">{desc}</Heading>
                </div>

                <Flex alignItems="center" justifyContent="start">
                    <Text bold marginRight="10px">City: </Text>
                    <Text>{city}</Text>
                </Flex>

                <Flex alignItems="center" justifyContent="start">
                    <Text bold marginRight="10px">Owner:</Text>
                    <LinkExternal href={getChainExplorerUrl(chainId, owner)}>{truncateWalletAddress(owner)}</LinkExternal>
                </Flex>

                {
                    twitter && <Flex alignItems="center" justifyContent="start">
                        <Text bold marginRight="10px">Twitter:</Text>
                        <Link href={`https://twitter.com/${twitter}`} target="_blank">@{twitter}</Link>
                    </Flex>
                }

                <Flex alignItems="center" justifyContent="start">
                    <Text bold marginRight="10px">Bid Level:</Text>
                    <Text>{bidLevel}</Text>
                </Flex>

                <Flex alignItems="center" justifyContent="start">
                    <Text bold marginRight="10px">Bid Price:</Text>
                    <Text>{formatedBasePrice * bidLevel} LEEK</Text>
                </Flex>

                {
                    account ?
                        <Flex alignItems="center" justifyContent="space-between">
                            <Button size="sm" mt="10px" onClick={bid}>Bid Here</Button>
                            <Button size="sm" mt="10px" disabled={!isQualified} onClick={generateNFT}>Generate NFT</Button>
                        </Flex>
                        :
                        <UnlockButton size="sm" mt="10px" />
                }

                {
                    account && !isQualified && <Text color="failure" mt="10px">Sorry! Only owner can generate NFT</Text>
                }

            </Box>
        </BillboardLayout >
    )
}

export default React.memo(BillboardDetails)